@import "styles/main";


  .booking {
    .payment {
      &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #fff;
        padding: 1rem 1.5rem;
        margin: 1rem;
        border-radius: 0.3rem;
        box-shadow: 1px 1px 1px 0 #0000000d;


        &-content {
          padding-left: 1rem;

          h1 {
            font-size: 1.1rem;
            font-weight: 600;
          }

          p {
            color: #999999;
          }
        }

        &-img {
          display: block;
          width: 6rem;

          img {
            display: block;
            width: 100%;
          }
        }


        [type="radio"]:checked,
        [type="radio"]:not(:checked) {
          position: absolute;
          left: -9999px;
        }

        [type="radio"]:checked+label,
        [type="radio"]:not(:checked)+label {
          position: relative;
          padding-left: 28px;
          cursor: pointer;
          line-height: 20px;
          display: inline-block;
          color: #666;
        }

        [type="radio"]:checked+label:before,
        [type="radio"]:not(:checked)+label:before {
          content: '';
          position: absolute;
          left: 0;
          top: 30%;
          width: 18px;
          height: 18px;
          border: 1px solid #ddd;
          border-radius: 100%;
          background: #fff;
        }

        [type="radio"]:checked+label:after,
        [type="radio"]:not(:checked)+label:after {
          content: '';
          width: 12px;
          height: 12px;
          background: $primary;
          position: absolute;
          top: calc(30% + 3px);
          left: 3px;
          border-radius: 100%;
          -webkit-transition: all 0.2s ease;
          transition: all 0.2s ease;
        }

        [type="radio"]:not(:checked)+label:after {
          opacity: 0;
          -webkit-transform: scale(0);
          transform: scale(0);
        }

        [type="radio"]:checked+label:after {
          opacity: 1;
          -webkit-transform: scale(1);
          transform: scale(1);
        }
      }

    }

    .btn-payNow {
      @include button-custom-v($white, $primary);
      font-size: 1.2rem;
      padding: 0.7rem 1rem;
      border-radius: 4px;
      margin-left: 15rem;
      margin-top: 2rem;
    }

    .ant-form-item-control-input-content {
      padding-left: 0.5rem !important;
    }
  }
